<template>
  <div class="tabs">
            <div class="tabs-bar">
                <!-- v-for, :class class-->
                <div :class="tabCls(item)" v-for="(item,index) in navList" @click="handleChange(index)">
                    {{item.label}}
                </div>
            </div>
            <div class="tabs-content">
                <!--slotpane-->
                <slot></slot>
            </div>
        </div>
</template>
<script>
export default {
    name: 'zoom-tab',
    props: {
        op: {
            type: Object,
            data: {
                type: Array,
                default: []
            }
        },
        hideTree: {
            type: Boolean,
            default: false
        }
    },
    data: function() {
        return {
            //pane
            navList: [],
            //valuecurrentValue
            currentValue: this.value
        }
    },
    props: {
        //value
        value: {
            type: [String]
        }
    },
    methods: {
        //$childrenpane
        getTabs: function() {
            return this.$children.filter(function(item) {
                return item.$options.name === 'pane';
            })
        },
        //tabs
        updateNav() {
            this.navList = [];
            var _this = this;
            this.getTabs().forEach(function(pane, index) {
                _this.navList.push({
                    label: pane.label,
                    name: pane.name || index
                });
                //name
                if(!pane.name) {
                    pane.name = index;
                }
                //panetab
                if(index === 0) {
                    if(!_this.currentValue) {
                        _this.currentValue = pane.name || index;
                    }
                }
            });
            this.updateStatus();
        },
        updateStatus() {
            var tabs = this.getTabs();
            var _this = this;
            //tabpane
            tabs.forEach(function(tab) {
                return tab.show = tab.name === _this.currentValue;
            })
        },
        tabCls: function(item) {
            return [
                'tabs-tab',
                {
                    //tabtabs-tab-active class
                    'tabs-tab-active': item.name === this.currentValue
                }
            ]
        },
        //tab
        handleChange: function(index) {
            var nav = this.navList[index];
            var name = nav.name;
            //tabwatch
            this.currentValue = name;
            //
            this.$emit('input', name);
        }
    },
    watch: {
        value: function(val) {
            this.currentValue = val;
        },
        currentValue: function() {
            //tabpane
            this.updateStatus();
        }
    }
}
</script>
<style>
[v-cloak]{
    display: none;
}
.tabs{
    font-size: 14px;
    color: #657180;
}
.tabs-bar:after{
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background: #d7dde4;
    margin-top:-1px;
}
.tabs-tab{
    display: inline-block;
    padding: 4px 16px;
    margin-right: 6px;
    background: #fff;
    border: 1px solid #d7dde4;
    cursor: pointer;
    position: relative;
}
.tabs-tab-active{
    color: #3399ff;
    border-top: 1px solid #3399ff;
    border-bottom: 1px solid #3399ff;
}
.tabs-tab-active:before{
    content: '';
    display: block;
    height: 1px;
    background: #3399ff;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}
.tabs-content{
    padding: 8px 0;
}
</style>
